import axios from "axios";
import { URLS } from "../constants/consts";

const JUN_API_BASE_URL = URLS.baseURL+"/juniorpurplesocieties";

class JuniorUserService {

    getLists() {
        return axios.get(JUN_API_BASE_URL);
    }

    create(data) {
        return axios.post(JUN_API_BASE_URL, data);
    }

    getById(id) {
        return axios.get(JUN_API_BASE_URL + '/' + id);
    }

    update(data, id) {
        return axios.put(JUN_API_BASE_URL + '/' + id, data);
    }

    delete(id) {
        return axios.delete(JUN_API_BASE_URL + '/' + id);
    }
}

export default new JuniorUserService();
