import axios from "axios";
import { URLS } from "../constants/consts";

const VOLUNTEER_API_BASE_URL = URLS.baseURL+"/volunteers";

class LeadershipTestimonialService {

    getLists() {
        return axios.get(VOLUNTEER_API_BASE_URL);
    }

    create(data) {
        return axios.post(VOLUNTEER_API_BASE_URL, data);
    }

    getById(id) {
        return axios.get(VOLUNTEER_API_BASE_URL + '/' + id);
    }

    update(data, id) {
        return axios.put(VOLUNTEER_API_BASE_URL + '/' + id, data);
    }

    delete(id) {
        return axios.delete(VOLUNTEER_API_BASE_URL + '/' + id);
    }
}

export default new LeadershipTestimonialService();
