import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import ContactService from '../../../services/ContactService';

function Contacts(props) {

    var [contacts, setContacts] = useState([]);

    useEffect(() => {
        ContactService.getLists().then((res) => {
            setContacts(res.data);
        }).catch((err) => {
            console.error('Error getting contacts', err);
        })
    }, []);

    const addContact = () => {
        props.history.push(`/add-contacts/_add`);
    }

    const editContact = (id) => {
        props.history.push(`/add-contacts/${id}`);
    }

    const deleteContact = async (id) => {
        if (!window.confirm("Confirm Contact deletion attempt!")) {
            return;
        }

        await ContactService.delete(id);
        let res = await ContactService.getLists();
        setContacts(res.data);
    }

    return (
        <div className='w-full max-w-[900px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Contact List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addContact()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Contact
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Name </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Email </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Phone </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Message </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {contacts.map((contact, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {contact.first_name + ' ' + contact.last_name} </td>
                                <td className="h-full p-3 border border-slate-300"> {contact.email} </td>
                                <td className="h-full p-3 border border-slate-300"> {contact.phone} </td>
                                <td className="h-full p-3 border border-slate-300"> {contact.message} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editContact(contact._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteContact(contact._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(Contacts);
