import React, { useEffect, useState } from 'react';

import { URLS } from '../../../constants/consts';
import { withRouter } from 'react-router-dom';

import LeadershipTeamService from '../../../services/LeadershipTeamService';

function LeadershipTeam(props) {
    const [leadershipteams, setLeadershipTeams] = useState([]);

    useEffect(() => {
        LeadershipTeamService.getLists().then(res => {
            setLeadershipTeams(res.data.leadershipteams);
        }).catch(err => {
            console.error('Error getting leadership teams', err);
        });
    }, []);

    const addLeader = () => {
        props.history.push(`/add-leadershipteam/_add`);
    }

    const editLeader = (id) => {
        props.history.push(`/add-leadershipteam/${id}`);
    }

    const deleteLeader = async (id) => {
        try {
            if (!window.confirm('Want to delete really?'))
                return;
            else {
                await LeadershipTeamService.delete(id);
                const res = await LeadershipTeamService.getLists();
                setLeadershipTeams(res.data.leadershipteams);
            }
        } catch (err) {
            console.error('Error deleting leadership team', err);
        }
    }

    return (
        <div className='w-full max-w-[768px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Leadership Team List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addLeader()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Leader
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Name </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Designation </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Picture </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Description </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {leadershipteams.map((leader, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {leader.name} </td>
                                <td className="h-full p-3 border border-slate-300"> {leader.designation} </td>
                                <td className="h-full p-3 border border-slate-300">
                                    <img className="main-img"
                                        src={URLS.imageURL + '/uploads/leadershipteams/' + leader.picture}
                                        alt="" />
                                </td>
                                <td className="h-full p-3 border border-slate-300"> {leader.description} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editLeader(leader._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteLeader(leader._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(LeadershipTeam);
