import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import ContactService from '../../../services/ContactService';

function CreateJuniorUser(props) {

  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [message, setMessage] = useState('');

  const path = props.location.pathname;
  const contact_id = path.slice(path.lastIndexOf('/') + 1);

  useEffect(() => {
    if (contact_id !== '_add') {
      ContactService.getById(contact_id).then((res) => {
        setFirstName(res.data.first_name);
        setLastName(res.data.last_name);
        setEmail(res.data.email);
        setPhone(res.data.phone);
        setMessage(res.data.message);
      })
    }
  }, []);

  const handleContactSubmit = async () => {
    try {
      const data = {
        first_name: firstName,
        last_name: lastName,
        email, phone, message,
      }

      if (contact_id === '_add') {
        await ContactService.create(data);
      } else {
        await ContactService.update(data, contact_id);
      }

      props.history.push('/contacts');

    } catch (err) {
      console.error('Error handling Update', err);
    }
  }

  return (
    <div className='w-full max-w-[768px] text-center mx-auto pt-16'>
      <div className='max-w-[600px] mx-auto bg-white p-3 border border-gray-300 shadow-md rounded-lg'>
        <h1 className='text-3xl font-semibold'>
          Add | Update Junior Purple User
        </h1>
        <div className='flex flex-col justify-center items-center text-start p-6'>
          <div className='w-full py-2'>
            <label> First Name: </label>
            <input
              value={firstName}
              onChange={(e) => { setFirstName(e.target.value) }}
              className='form-control'
              placeholder="First Name"
              name="first_name"
            />
          </div>

          <div className='w-full py-2'>
            <label> Last Name: </label>
            <input
              value={lastName}
              onChange={(e) => { setLastName(e.target.value) }}
              className='form-control'
              name="last_name"
              placeholder="Last Name"
            >
            </input>
          </div>

          <div className='w-full py-2'>
            <label> Email: </label>
            <input
              value={email}
              onChange={(e) => { setEmail(e.target.value) }}
              className='form-control'
              name="email"
              placeholder="Email address"
            >
            </input>
          </div>

          <div className='w-full py-2'>
            <label> Phone: </label>
            <input
              value={phone}
              onChange={(e) => { setPhone(e.target.value) }}
              className='form-control'
              name="phone"
              placeholder="Phone Number"
            >
            </input>
          </div>

          <div className='w-full py-2'>
            <label> Message: </label>
            <textarea
              value={message}
              onChange={(e) => { setMessage(e.target.value) }}
              rows="5"
              className='form-control'
              name="message"
              placeholder="Leave us a message ..."
            >
            </textarea>
          </div>

          <div className='flex justify-start w-full gap-3 p-2'>
            <button
              className="btn btn-success p-2"
              onClick={() => handleContactSubmit()}
            >
              Save
            </button>
            <button
              onClick={() => { props.history.push('/contacts') }}
              className="btn btn-danger p-2"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default withRouter(CreateJuniorUser);