import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import LeadershipTestimonialService from '../../../services/LeadershipTestimonialService';

function LeadershipTestimonial(props) {

    var [leadershiptestimonials, setLeadershipTestimonials] = useState([]);

    useEffect(() => {
        LeadershipTestimonialService.getLists().then((res) => {
            setLeadershipTestimonials(res.data.leadershiptestimonials);
        }).catch((err) => {
            console.error('Error getting leadership testimonial', err);
        })
    }, []);

    const addLeadershipTestimonial = () => {
        props.history.push(`/add-leadershiptestimonial/_add`);
    }

    const editLeadershipTestimonial = (id) => {
        props.history.push(`/add-leadershiptestimonial/${id}`);
    }

    const deleteLeadershipTestimonial = async (id) => {
        if (!window.confirm("Confirm Leadership Testimonial deletion attempt!")) {
            return;
        }

        await LeadershipTestimonialService.delete(id);
        let res = await LeadershipTestimonialService.getLists();
        setLeadershipTestimonials(res.data.leadershiptestimonials);
    }

    return (
        <div className='w-full max-w-[900px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Leadership Testimonial List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addLeadershipTestimonial()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Testimonial
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Name </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Note </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Description </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {leadershiptestimonials.map((testimonial, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {testimonial.name} </td>
                                <td className="h-full p-3 border border-slate-300"> {testimonial.note} </td>
                                <td className="h-full p-3 border border-slate-300"> {testimonial.description} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editLeadershipTestimonial(testimonial._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteLeadershipTestimonial(testimonial._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(LeadershipTestimonial);
