import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import LeadershipTestimonialService from '../../../services/LeadershipTestimonialService';

function CreateLeadershipTestimonial(props) {

  const [name, setName] = useState('');
  const [note, setNote] = useState('');
  const [description, setDescription] = useState('');
  const [content, setContent] = useState('');

  const path = props.location.pathname;
  const testimonial_id = path.slice(path.lastIndexOf('/') + 1);

  useEffect(() => {
    if (testimonial_id !== '_add') {
      LeadershipTestimonialService.getById(testimonial_id).then((res) => {
        setName(res.data.name);
        setNote(res.data.note);
        setDescription(res.data.description);
        setContent(res.data.content);
      })
    }
  }, []);

  const handleTestimonialSubmit = async () => {
    try {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("note", note);
      formData.append("description", description);
      formData.append("content", content);


      if (testimonial_id === '_add') {
        await LeadershipTestimonialService.create(formData);
      } else {
        await LeadershipTestimonialService.update(formData, testimonial_id);
      }

      props.history.push('/leadershiptestimonials');

    } catch (err) {
      console.error('Error handling Update');
    }
  }

  return (
    <div className='w-full max-w-[768px] text-center mx-auto pt-16'>
      <div className='max-w-[600px] mx-auto bg-white p-3 border border-gray-300 shadow-md rounded-lg'>
        <h1 className='text-3xl font-semibold'>
          Add | Update Leadership Testimonial
        </h1>
        <div className='flex flex-col justify-center items-center text-start p-6'>
          <div className='w-full py-2'>
            <label> Name: </label>
            <input
              value={name}
              onChange={(e) => { setName(e.target.value) }}
              className='form-control'
              placeholder="Name"
              name="name"
            />
          </div>

          <div className='w-full py-2'>
            <label> Note: </label>
            <textarea
              value={note}
              onChange={(e) => { setNote(e.target.value) }}
              rows="5"
              className='form-control'
              name="note"
              placeholder="Leave us a note..."
            >
            </textarea>
          </div>

          <div className='w-full py-2'>
            <label> Description: </label>
            <textarea
              value={description}
              onChange={(e) => { setDescription(e.target.value) }}
              rows="5"
              className='form-control'
              name="description"
              placeholder="Leave us a description..."
            >
            </textarea>
          </div>

          <div className='w-full py-2'>
            <label> Content: </label>
            <textarea
              value={content}
              onChange={(e) => { setContent(e.target.value) }}
              rows="5"
              className='form-control'
              name="content"
              placeholder="Input a HTML Content ..."
            >
            </textarea>
          </div>

          <div className='flex justify-start w-full gap-3 p-2'>
            <button
              className="btn btn-success p-2"
              onClick={() => handleTestimonialSubmit()}
            >
              Save
            </button>
            <button
              onClick={() => { props.history.push('/leadershiptestimonials') }}
              className="btn btn-danger p-2"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default withRouter(CreateLeadershipTestimonial);