import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import VolunteerService from '../../../services/VolunteerService';

function Volunteer(props) {

    var [volunteers, setVolunteers] = useState([]);

    useEffect(() => {
        VolunteerService.getLists().then((res) => {
            setVolunteers(res.data);
        }).catch((err) => {
            console.error('Error getting volunteers', err);
        })
    }, []);

    const addVolunteer = () => {
        props.history.push(`/add-volunteer/_add`);
    }

    const editVolunteer = (id) => {
        props.history.push(`/add-volunteer/${id}`);
    }

    const deleteVolunteer = async (id) => {
        if (!window.confirm("Confirm Volunteer deletion attempt!")) {
            return;
        }

        await VolunteerService.delete(id);
        let res = await VolunteerService.getLists();
        setVolunteers(res.data);
    }

    return (
        <div className='w-full max-w-[900px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Volunteers List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addVolunteer()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Volunteer
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Name </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Email </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Phone </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Message </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {volunteers.map((volunteer, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {volunteer.first_name + ' ' + volunteer.last_name} </td>
                                <td className="h-full p-3 border border-slate-300"> {volunteer.email} </td>
                                <td className="h-full p-3 border border-slate-300"> {volunteer.phone} </td>
                                <td className="h-full p-3 border border-slate-300"> {volunteer.message} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editVolunteer(volunteer._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteVolunteer(volunteer._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(Volunteer);
