import React, { useEffect, useRef, useState } from 'react';
import { withRouter } from 'react-router-dom';

import LeadershipTeamService from '../../../services/LeadershipTeamService';
import FileUpload from '../../../components/Forms/Elements/FileUpload';
import { URLS } from '../../../constants/consts';

function CreateLeadershipTeam(props) {

    const [name, setName] = useState('');
    const [designation, setDesignation] = useState('');
    const [description, setDescription] = useState('');
    const [picture, setPicture] = useState('');
    const [uploadImage, setUploadImage] = useState(null);

    const path = props.location.pathname;
    const leadership_id = path.slice(path.lastIndexOf('/') + 1);

    useEffect(() => {
        if (leadership_id !== '_add') {
            LeadershipTeamService.getById(leadership_id).then((res) => {
                let leader = res.data;
                setName(leader.name);
                setDesignation(leader.designation);
                setDescription(leader.description);
                setPicture(leader.picture);
            })
        }
    }, []);

    const handleLeadershipSubmit = async () => {
        try {
            const formData = new FormData();
            formData.append("name", name);
            formData.append("designation", designation);
            formData.append("description", description);

            if (uploadImage) {
                formData.append("image", uploadImage);
            }

            if (leadership_id === '_add') {
                await LeadershipTeamService.create(formData);
            } else {
                await LeadershipTeamService.update(formData, leadership_id);
            }

            props.history.push('/leadershipteams');

        } catch (err) {
            console.error('Error handling Update');
        }
    }

    const addUploadImage = (image) => {
        setUploadImage(image);
    }

    return (
        <div className='w-full max-w-[768px] text-center mx-auto pt-16'>
            <div className='max-w-[600px] mx-auto bg-white p-3 border border-gray-300 shadow-md rounded-lg'>
                <h1 className='text-3xl font-semibold'>
                    Add | Update Leadership Team 
                </h1>
                <div className='flex flex-col justify-center items-center text-start p-6'>
                    <div className='w-full py-2'>
                        <label> Name: </label>
                        <input
                            value={name}
                            onChange={(e) => { setName(e.target.value) }}
                            className='form-control'
                            placeholder='Name'
                            name='name'
                        />
                    </div>
                    <div className='w-full py-2'>
                        <label> designation: </label>
                        <input
                            value={designation}
                            onChange={(e) => { setDesignation(e.target.value) }}
                            className='form-control'
                            placeholder='designation'
                            name='designation'
                        />
                    </div>
                    <div className='w-full py-2'>
                        <label> Description: </label>
                        <textarea
                            value={description}
                            onChange={(e) => { setDescription(e.target.value) }}
                            rows="5"
                            className='form-control'
                            name="description"
                            placeholder="Leave us a description..."
                        >
                        </textarea>
                    </div>

                    <div className="w-full py-2">
                        <label> Picture: </label>
                        <img src={URLS.imageURL + '/uploads/leadershipteams/' + picture} alt='original' />
                        <FileUpload onChangeImage={addUploadImage} />
                    </div>

                    <div className='flex justify-start w-full gap-3 p-2'>
                        <button
                            className="btn btn-success p-2"
                            onClick={() => handleLeadershipSubmit()}
                        >
                            Save
                        </button>
                        <button
                            onClick={() => { props.history.push('/leadershipteams') }}
                            className="btn btn-danger p-2"
                        >
                            Cancel
                        </button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withRouter(CreateLeadershipTeam);