import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import JuniorUserService from '../../../services/JuniorPurpleUserService';

function JuniorUsers(props) {

    var [juniorusers, setJuniorUsers] = useState([]);

    useEffect(() => {
        JuniorUserService.getLists().then((res) => {
            setJuniorUsers(res.data);
        }).catch((err) => {
            console.error('Error getting juniorusers', err);
        })
    }, []);

    const addJuniorUser = () => {
        props.history.push(`/add-juniorpurplesocieties/_add`);
    }

    const editJuniorUser = (id) => {
        props.history.push(`/add-juniorpurplesocieties/${id}`);
    }

    const deleteJuniorUser = async (id) => {
        if (!window.confirm("Confirm Junior User deletion attempt!")) {
            return;
        }

        await JuniorUserService.delete(id);
        let res = await JuniorUserService.getLists();
        setJuniorUsers(res.data);
    }

    return (
        <div className='w-full max-w-[900px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Junior Users List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addJuniorUser()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Junior User
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Name </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Email </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Phone </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Message </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {juniorusers.map((junior, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {junior.first_name + ' ' + junior.last_name} </td>
                                <td className="h-full p-3 border border-slate-300"> {junior.email} </td>
                                <td className="h-full p-3 border border-slate-300"> {junior.phone} </td>
                                <td className="h-full p-3 border border-slate-300"> {junior.message} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editJuniorUser(junior._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteJuniorUser(junior._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(JuniorUsers);
