import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import NewsletterService from '../../../services/NewsletterService';

function Newsletters(props) {

    var [newsletters, setNewsletters] = useState([]);

    useEffect(() => {
        NewsletterService.getLists().then((res) => {
            setNewsletters(res.data);
        }).catch((err) => {
            console.error('Error getting newsletters', err);
        })
    }, []);

    const addNewsletter = () => {
        props.history.push(`/add-newsletters/_add`);
    }

    const editNewsletter = (id) => {
        props.history.push(`/add-newsletters/${id}`);
    }

    const deleteNewsletter = async (id) => {
        if (!window.confirm("Confirm Newsletter deletion attempt!")) {
            return;
        }

        await NewsletterService.delete(id);
        let res = await NewsletterService.getLists();
        setNewsletters(res.data);
    }

    return (
        <div className='w-full max-w-[900px] text-center mx-auto pt-16'>
            <h1 className='font-semibold text-3xl'>
                Newsletter List
            </h1>
            <div className='w-full flex flex-col justify-center items-start my-9 p-3'>
                <button
                    onClick={() => addNewsletter()}
                    className='btn btn-primary mb-3 p-3'
                >
                    Add Newsletter
                </button>

                <table className='w-full text-sm text-start border border-collapse border-slate-400'>
                    <thead>
                        <tr>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Email </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Created At </th>
                            <th className='p-3 bg-gray-100 border border-slate-300'> Actions </th>
                        </tr>
                    </thead>
                    <tbody>
                        {newsletters.map((newsletter, index) => (
                            <tr className='hover:bg-gray-200' key={index}>
                                <td className="h-full p-3 border border-slate-300"> {newsletter.email} </td>
                                <td className="h-full p-3 border border-slate-300"> {newsletter.createdAt} </td>
                                <td className="h-full p-3 border border-b-0 border-t-slate-300">
                                    <div className='h-full gap-3 flex flex-col'>
                                        <button
                                            className="btn btn-info"
                                            onClick={() => editNewsletter(newsletter._id)}
                                        >
                                            Update
                                        </button>
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => deleteNewsletter(newsletter._id)}
                                        >
                                            Delete
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default withRouter(Newsletters);
