import React, { useEffect, useState } from 'react';
import { withRouter } from 'react-router-dom';

import NewsletterService from '../../../services/NewsletterService';

function CreateNewsletter(props) {

  const [email, setEmail] = useState('');

  const path = props.location.pathname;
  const newsletter_id = path.slice(path.lastIndexOf('/') + 1);

  useEffect(() => {
    if (newsletter_id !== '_add') {
      NewsletterService.getById(newsletter_id).then((res) => {
        setEmail(res.data.email);
      })
    }
  }, []);

  const handleNewsletterSubmit = async () => {
    try {
      const data = { email };

      if (newsletter_id === '_add') {
        await NewsletterService.create(data);
      } else {
        await NewsletterService.update(data, newsletter_id);
      }

      props.history.push('/newsletters');

    } catch (err) {
      console.error('Error handling Update', err);
    }
  }

  return (
    <div className='w-full max-w-[768px] text-center mx-auto pt-16'>
      <div className='max-w-[600px] mx-auto bg-white p-3 border border-gray-300 shadow-md rounded-lg'>
        <h1 className='text-3xl font-semibold'>
          Add | Update Newsletter
        </h1>
        <div className='flex flex-col justify-center items-center text-start p-6'>
          
          <div className='w-full py-2'>
            <label> Email: </label>
            <input
              value={email}
              onChange={(e) => { setEmail(e.target.value) }}
              className='form-control'
              name="email"
              placeholder="Email address"
            >
            </input>
          </div>

          <div className='flex justify-start w-full gap-3 p-2'>
            <button
              className="btn btn-success p-2"
              onClick={() => handleNewsletterSubmit()}
            >
              Save
            </button>
            <button
              onClick={() => { props.history.push('/newsletters') }}
              className="btn btn-danger p-2"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default withRouter(CreateNewsletter);