import React, { useEffect } from 'react';
import Sidebar from '../../components/Theme-Corporate/SidebarCorporate'
import { useSelector } from "react-redux";
import { withRouter } from 'react-router-dom';

function Home(props) {

    const isAuthenticated = useSelector(state => state.AuthReducer.isAuthenticated);

    // useEffect(() => {
    //     if (!isAuthenticated) {
    //         props.history.push("/login");
    //     }
    // })

    return (
        <div className='flex flex-col items-center w-full max-w-[768px] text-center mx-auto pl-20 pt-20'>
            <img
                src='assets/img/purple_logo.png'
                className='w-[300px]' alt='logo'
            />

            <h1 className='my-20 text-4xl font-bold'>
                Welcome The Purple Society
            </h1>

            {isAuthenticated ? (
                <div className='w-full bg-white px-6 py-9 rounded-lg flex gap-3 flex-wrap justify-center'>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/ourworks')}
                    >
                        Our work Gallery
                    </button>
                    <button 
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/leadershipteams')}
                    >
                        Team
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/leadershiptestimonials')}
                    >
                        Testimonial
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/supports')}
                    >
                        Support
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/nitaliablankets')}
                    >
                        Nitalia Blanket
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/purpleapartments')}
                    >
                        Purple Apartment
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/juniors')}
                    >
                        Junior Purple Society
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/volunteers')}
                    >
                        Volunteer
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/juniorpurplesocieties')}
                    >
                        Junior Purple User
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/contacts')}
                    >
                        Contact
                    </button>
                    <button
                        className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                        onClick={() => props.history.push('/newsletters')}
                    >
                        Newsletter
                    </button>
                </div>
            ) : (
                <button
                    onClick={() => props.history.push("/login")}
                    className='text-white bg-purple-700 p-3 text-lg rounded-lg'
                >
                    Get Started
                </button>
            )}
        </div>
    )
}

export default withRouter(Home);
